<script>
import Draggable from "vuedraggable";

import top from "@/assets/svg/top.svg";
import Editable from "./components/Editable.vue";
import Toggle from "./components/Toggle.vue";
import Item from "./components/Item.vue";

export default {
  name: "App",
  components: { Toggle, Item, Editable, Draggable },
  data() {
    return {
      color: "#ff00ff",
      defaultColors: [
        "#F3385A",
        "#FF551F",
        "#FFE299",
        "#6CC65E",
        "#A4E6F4",
        "#3960C6",
        "#BD00FF",
        "#A40000",
        "#B3B3B3",
        "#000000",
      ],
      showTemplateSelect: false,
      templateSelect: "Vianon ponuka",
      selectOptions: ["Vianon ponuka", "awef", "awef"],
      drag: false,
      title: "",
      welcomeText: "",
      companyName: "",
      companyStreet: "",
      companyTownZipcode: "",
      ico: "",
      dic: "",
      icDph: "",
      showSummaryNumbers: true,
      addTotalEnd: true,
      summaries: [],
      newSummaryTitle: "",
      newSummaryValue: "",
      top,
      totalDiscount: false,
      vatEnable: false,
      totalDiscountValue: 15,
      vatValue: 21,
      // quote settings
      approveDeclinePriceQuote: false,
      addDeliveryDate: false,
      showReviews: false,
      hideDofferoLogo: false,
      internalQuoteName: "",
      internalQuoteId: "",
      language: "Slovak",
      languageOptions: [
        { label: "Slovak", value: "sk", flag: "sk.svg" },
        { label: "English", value: "en", flag: "en_GB.svg" },
        { label: "Greek", value: "el", flag: "el.svg" },
      ],
      currency: "EUR",
      currencyOptions: ["USD", "GBP", "EUR"],
      validityDate: null,
      nameOfClient: "",
      clients: ["client 1", "client 2", "good client"],
      // end quote settings
      logo: null,
      initItem: {
        title: "",
        type: {
          value: 1,
          label: "Cena za hodinov sadzbu",
        },
        types: [
          {
            value: 1,
            label: "Cena za hodinov sadzbu",
          },
          {
            value: 2,
            label: "Cenov rozsah od - do",
          },
          {
            value: 3,
            label: "Hodinov rozsah od -do",
          },
          {
            value: 4,
            label: "Vlastn cena",
          },
          {
            value: 5,
            label: "Celkov cena z podpoloiek",
          },
        ],
        unitTypes: [
          {
            value: 1,
            label: "ks",
          },
          {
            value: 2,
            label: "MD",
          },
          {
            value: 3,
            label: "MH",
          },
          {
            value: 4,
            label: "Ns",
          },
        ],
        richText: "",
        hoursFrom: 0,
        hoursTo: 0,
        priceFrom: 0,
        priceTo: 0,
        rate: 0,
        hours: 0,
        count: 0,
        value: 0,
        unit: null,
        subItems: [],
        total: 0,
        totalWithSubitems: 0,
        totalWithSubitemsWithDiscount: 0,
        timestamp: null,
        note: null,
        hasDiscount: false,
        discount: 0, // percent
        doNotCountInTotal: false,
      },
      items: [],
    };
  },
  computed: {
    summariesTotal() {
      let total = 0;
      this.summaries.forEach(({ value }) => {
        total += parseFloat(value);
      });
      return isNaN(total) ? 0 : total;
    },
    total() {
      let finalTotal = { from: 0, to: 0 };
      this.items.forEach((item) => {
        if (!item.doNotCountInTotal) {
          if (
            item.totalWithSubitemsWithDiscount.from ||
            item.totalWithSubitemsWithDiscount.from === 0
          )
            finalTotal.from += parseFloat(
              item.totalWithSubitemsWithDiscount.from
            );
          if (
            item.totalWithSubitemsWithDiscount.to ||
            item.totalWithSubitemsWithDiscount.to === 0
          )
            finalTotal.to += parseFloat(item.totalWithSubitemsWithDiscount.to);
          else if (typeof item.totalWithSubitemsWithDiscount === "number") {
            finalTotal.from += parseFloat(item.totalWithSubitemsWithDiscount);
            finalTotal.to += parseFloat(item.totalWithSubitemsWithDiscount);
          }
        }
      });
      if (finalTotal.from === finalTotal.to) finalTotal.sum = finalTotal.from;
      return finalTotal;
    },
    totalWithDiscount() {
      const percentage = (100 - parseFloat(this.totalDiscountValue)) / 100;

      return {
        from: this.totalDiscount
          ? (this.total.from * percentage)?.toFixed(2)
          : this.total.from?.toFixed(2),
        to: this.totalDiscount
          ? (this.total.to * percentage)?.toFixed(2)
          : this.total.to?.toFixed(2),
        sum: this.totalDiscount
          ? (this.total.sum * percentage)?.toFixed(2)
          : this.total.sum?.toFixed(2),
      };
    },
    totalWith21VAT() {
      const total = this.totalWithDiscount;

      const percentage = (100 + parseFloat(this.vatValue)) / 100;

      return {
        from: parseFloat((total.from * percentage).toFixed(2)),
        to: parseFloat((total.to * percentage).toFixed(2)),
      };
    },
  },
  methods: {
    finish() {
      console.log(this);
    },
    clickCustomColor() {
      // this.color = '#eeeeee'
      document.querySelector(".color-input")?.click();
    },
    deleteItem(index, itemsContainer = this.items) {
      itemsContainer.splice(index, 1);
    },
    logoInput(e) {
      const input = e.target;

      const file = input?.files?.[0];
      if (file) this.logo = URL.createObjectURL(file);
    },
    addNewSummary() {
      this.summaries.push({
        title: this.newSummaryTitle,
        value: this.newSummaryValue,
      });

      this.clearSummaryInputs();
    },
    deleteSummary(index) {
      console.log("deletesummary: ", index);
      this.summaries.splice(index, 1);
    },
    clearSummaryInputs() {
      this.newSummaryTitle = "";
      this.newSummaryValue = "";
    },
    addNewItem() {
      this.items.push(
        JSON.parse(JSON.stringify({ ...this.initItem, timestamp: Date.now() }))
      );
    },
    moveItem(from, to, itemsContainer) {
      // remove `from` item and store it
      var f = itemsContainer.splice(from, 1)[0];
      // insert stored item into position `to`
      itemsContainer.splice(to, 0, f);
    },
    moveUp(index, itemsContainer = this.items) {
      this.moveItem(index, index - 1, itemsContainer);
    },
    moveDown(index, itemsContainer = this.items) {
      this.moveItem(index, index + 1, itemsContainer);
    },
    addSubItem(index) {
      this.items[index].subItems.push({
        ...this.initItem,
        subItems: null,
        timestamp: Date.now(),
      });
    },
    enableTooltips() {
      var tooltipTriggerList = [].slice.call(
        document.querySelectorAll('[data-bs-toggle="tooltip"]')
      );
      tooltipTriggerList.map(function (tooltipTriggerEl) {
        return new window.bootstrap.Tooltip(tooltipTriggerEl);
      });
    },
  },
  mounted() {
    this.addNewItem();

    this.enableTooltips();
  },
};
</script>

